<template>
  <div id="main">
		<ul class="items-list">
			<li class="items-box" v-for="item in showItems" :key="item.id">
				<img :src="require('@/assets/images/' + item.pic)" :alt="item.name" />
				<div class="name">{{item.name}}</div>
				<div class="price">￥ {{item.price.toFixed(1)}}</div>
				<div class="addcart">
					<a href="" @click.prevent="addToCart(item)">加入购物车</a>
					<transition name="addto"><span v-if="item.incart"></span></transition>
				</div>
			</li>
		</ul>
		
		<div class="page-container">
			<c-page :total="currentItems.length" :prepage="prepage" :page="page" @changepage="changePage" />
		</div>
	</div>
</template>

<script>
import items from '@/assets/js/d-items.js'
import CPage from '@/components/CPage.vue'

if(JSON.parse(localStorage.getItem('items')) == null)
localStorage.setItem('items',JSON.stringify(items));
if(JSON.parse(localStorage.getItem('cart')) == null)
localStorage.setItem('cart', '[]');

export default {
  name: 'Home',

  components: {
    CPage
  },

 data() {
    return {
      items: [],
      cart: [],
      category: '',
      currentItems: [],
      page: 1,
			prepage: 10
    }
  },

  mounted() {
    this.items = JSON.parse(localStorage.getItem('items'));
    this.cart = JSON.parse(localStorage.getItem('cart'));
    this.category = this.$route.params.category;
  },
  
  computed: {
    showItems: {
			get() {
        this.currentItems = this.items;
        if(this.category != '') {
          this.currentItems = this.items.filter(item => item.category == this.category)
        }
				let start = (this.page - 1) * this.prepage;
				let end = start + this.prepage;
				return this.currentItems.slice(start,end);
			}
		}
  },
  
  methods: {
    addToCart(item) {
      let cartItem = this.cart.find(i => i.id == item.id);
      if(!cartItem) {
        cartItem = {...item};
        cartItem.quan = 1;
        cartItem.check = false;
        this.cart.push(cartItem);
        this.$set(item,'incart',true);
        localStorage.setItem('items',JSON.stringify(this.items));
        localStorage.setItem('cart',JSON.stringify(this.cart));
      }else{
        cartItem.quan++;
        this.$set(item,'incart',false);
        const vm = this;
        setTimeout(function(){
          vm.$set(item,'incart',true);
          localStorage.setItem('items',JSON.stringify(vm.items));
          localStorage.setItem('cart',JSON.stringify(vm.cart));
        },50)
      }
      
    },
    
    changePage(p) {
      this.page = p;
    }
  }
}
</script>