<template>
  <div id="main">
    <div class="cart">
      <div class="cart-top">
        <div class="cart-left">
          <label>
            <input type="checkbox" v-model="checkAll" />
            <span>全选</span>
          </label>
        </div>
        <div class="cart-left">商品</div>
        <div class="cart-right">操作</div>
        <div class="cart-right">小计</div>
        <div class="cart-right">数量</div>
        <div class="cart-right">单价</div>
      </div>
      
      <ul class="cart-list">
        <li class="no-item" v-if="this.cart.length == 0">购物车竟然是空的！赶快去挑选喜欢的商品吧！</li>
        <li class="cart-item" v-for="item in cart" :key="item.id">
          <div class="item-checkbox">
            <input type="checkbox" :checked="item.check" @change="changeCheck(item,$event)" />
          </div>
          <div class="item-info">
            <img :src="require('@/assets/images/' + item.pic)" alt="" />
            <span class="item-name">{{item.name}}</span>
          </div>
          <div class="item-price">￥ {{item.price.toFixed(1)}}</div>
          <div class="item-quan">
            <span class="quan-add" @click="changeQuan(item,item.quan-1)">-</span>
            <input class="quan-num" :value="item.quan" @blur="changeQuan2(item,$event)" @keyup="onlyNum" @afterpaste="onlyNum" />
            <span class="quan-sub" @click="changeQuan(item,item.quan+1)">+</span>
          </div>
          <div class="item-total">￥ {{(item.quan * item.price).toFixed(1)}}</div>
          <div class="item-oper"><span @click="deleteItem(item)">删除</span></div>
        </li>
      </ul>
      
      <div class="cart-bottom">
        <div class="cart-left">
          <label>
            <input type="checkbox" v-model="checkAll" />
            <span>全选</span>
          </label>
          <a href="" @click.prevent="deleteChecked">删除选中</a>
        </div>
        <div class="cart-right butt" :class="{disabled: totalPrice <= 0}">立即支付</div>
        <div class="cart-right">
          一共选择了
          <span class="total-quan"> {{itemQuan}} </span>
          件商品，总计
          <span class="total-price"> ￥ {{totalPrice.toFixed(1)}} </span>
          元
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Cart',

  data() {
    return {
      items: [],
      cart: []
    }
  },

  mounted() {
    this.items = JSON.parse(localStorage.getItem('items'));
    this.cart = JSON.parse(localStorage.getItem('cart'));
  },

  computed: {
		checkAll: {
			get() {
				return this.cart.every(i => i.check);
			},
			set(newVal) {
        this.cart.forEach(i => {
          i.check = newVal;
        })
			}
		},
		
		itemQuan: {
			get() {
				return this.cart.filter(i => i.check).reduce(
				(total,current) => {
					return total + current.quan;
				},0);
			}
		},
		
		totalPrice: {
			get() {
				return this.cart.filter(i => i.check).reduce(
				(total,current) => {
					return total + current.quan * current.price;
				},0);
			}
		}
	},
	
	methods: {
		changeCheck(item,e) {
      this.cart.forEach(i => {
        if(i.id == item.id) {
          i.check = e.target.checked;
        }
      })
		},
		
		changeQuan(item,quan) {
			if(quan > 0){
        this.cart.forEach(i => {
          if(i.id == item.id) {
            i.quan = quan;
          }
        })
			}
    },
    
    changeQuan2(item,event) {
      if(event.target.value == '') {
        event.target.value = 1;
      }
      let quan = event.target.value;
      this.cart.forEach(i => {
        if(i.id == item.id) {
          i.quan = quan;
        }
      })
    },
		
		deleteItem(item) {
      let delNo = this.cart.indexOf(item);
      this.cart.splice(delNo,1);
      this.deleteDo(item);
		},
		
		deleteChecked() {
      for(let cartIndex = this.cart.length-1; cartIndex >= 0; cartIndex--) {
        let item = this.cart[cartIndex];
        if(item.check == true){
          this.cart.splice(cartIndex,1);
          this.deleteDo(item);
        }
      }
    },

    deleteDo(item) {
      this.items.forEach(i => {
        if(i.id == item.id) {
          i.incart = false;
        }
      });
      localStorage.setItem('items',JSON.stringify(this.items));
      localStorage.setItem('cart',JSON.stringify(this.cart));
    },

    onlyNum(event) {
      event.target.value = event.target.value.replace(/^(0+)|[^\d]+/g,'');
    }
  }
}

</script>