<template>
	<div class="page">
		<a href="" @click.prevent="changePage(page - 1)">上一页</a>
		<a href="" @click.prevent="changePage(p)" :class="{current:p==page}" v-for="p in pages" :key="p">{{p}}</a>
		<a href="" @click.prevent="changePage(page + 1)">下一页</a>
	</div>
</template>

<script>
export default {
	name: 'CPage',

	props: {
		total: {
			type: Number,
			default: 0
		},
		prepage: {
			type: Number,
			default: 10
		},
		page: {
			type: Number,
			default: 1
		}
	},
	
	computed: {
		pages: {
			get() {
				return Math.ceil(this.total / this.prepage)
			}
		}
	},
	
	methods: {
		changePage(p) {
			if(p > 0 && p <= this.pages && p != this.page) {
				this.$emit('changepage',p);
			}
		}
	}
}
</script>